body {
  font-size: 14px;
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 20px;
}

.search-form {
  width: 100%;
  background-color: beige;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 20px 0;
  display: flex;
  position: fixed;
  left: 0;
}

.search-form input {
  width: 400px;
  border: 1px solid #212121;
  border-radius: 5px;
  outline: none;
  padding: 15px;
  transition-property: color, border-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 4px rgba(234, 226, 204, .15);
}

.search-form input:hover, .search-form input:focus {
  border: 1px solid #fffdc8;
}

button {
  min-width: 130px;
  letter-spacing: .04em;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 14px;
  font-weight: 400;
  transition-property: fill, border-color, background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

button:hover {
  background-color: beige;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  padding: 100px 0 20px;
  display: flex;
}

.load-more-wrapper {
  padding-bottom: 20px;
}

.load-more {
  margin: 0 auto;
}

.is-hidden {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  position: absolute;
  overflow: hidden;
}

.photo-card {
  flex-basis: 350px;
}

.photo-card img {
  cursor: pointer;
  display: block;
}

.info {
  flex-flow: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

